body {
  background-color: #fbfcf8;
  flex-direction: row;
  height: 100vh;
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
  display: flex;
}

.github-icon {
  padding: 10px;
  position: fixed;
  top: 0;
  left: 0;
}

.container {
  text-align: center;
  flex-grow: 1;
  margin-right: 200px;
  padding: 10px;
  overflow-y: auto;
}

.debug {
  background-color: #f1f1f1;
  width: 200px;
  padding: 10px;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  overflow-y: scroll;
  box-shadow: -2px 0 5px #00000080;
}

.footnote {
  text-align: left;
  margin-top: 10px;
  padding: 10px;
  font-size: 14px;
}

button {
  color: #000;
  background-color: #fff;
  border-width: 1px;
  border-radius: 5px;
  width: 100px;
  height: 70px;
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 10px;
  padding: 4px;
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
  font-size: 18px;
}

button:hover {
  background-color: #e9e6dd;
}

button:disabled {
  cursor: not-allowed;
  color: #666;
  background-color: #ccc;
}

#midi-player {
  width: inherit;
  margin: 10px 15vw;
  display: block;
}

#midi-player::part(control-panel) {
  background: #f6f3e7;
  border: 2px solid #000;
  border-radius: 10px;
}

#midi-player::part(play-button) {
  color: #4c392d;
  background-color: #c9a88d;
  border: 2px solid;
  border-radius: 20px;
  transition: all .2s;
}

#midi-player::part(play-button):hover {
  color: #4c392d;
  background-color: #dddad3;
  border-radius: 10px;
}

#midi-player::part(time) {
  font-family: monospace;
}

#midi-player::part(loading-overlay) {
  color: #000;
  background: #000;
}

#midi-visualizer .piano-roll-visualizer {
  background: #fbf4ef;
  border: 2px solid #fbfcf8;
  border-radius: 25px 25px 10px 10px;
  margin: -5px 10vw 15px;
  overflow: auto;
}

#midi-visualizer svg rect.note {
  opacity: .6;
  stroke-width: 2px;
  stroke: #500;
}

#midi-visualizer svg rect.note[data-instrument="0"] {
  fill: #758c6f;
}

#midi-visualizer svg rect.note[data-instrument="1"] {
  fill: #a36c6c;
}

#midi-visualizer svg rect.note[data-instrument="2"] {
  fill: #de997f;
}

#midi-visualizer svg rect.note[data-instrument="3"] {
  fill: #8cb5c3;
}

#midi-visualizer svg rect.note[data-instrument="4"] {
  fill: #b38f68;
}

#midi-visualizer svg rect.note[data-instrument="5"] {
  fill: #adad9d;
}

#midi-visualizer svg rect.note[data-instrument="6"] {
  fill: #5c9090;
}

#midi-visualizer svg rect.note[data-instrument="7"] {
  fill: #c5bbde;
}

#midi-visualizer svg rect.note[data-instrument="8"] {
  fill: #d09b8a;
}

#midi-visualizer svg rect.note[data-is-drum="true"] {
  fill: #888;
  stroke: #888;
}

#midi-visualizer svg rect.note.active {
  opacity: .9;
  stroke: #000;
}

.testing_button {
  width: 200px;
}

.main-content {
  flex-grow: 1;
  padding: 10px;
  overflow-y: auto;
}
